@page {
    margin: 0;
}
body {
    width: 100vw;
    max-width: 100%;
    background-color: black;
    min-height: 279mm;
    font-family: 'Hind', sans-serif;
    margin: 0;
    padding: 0;
    -webkit-print-color-adjust: exact;

    font-weight: 400;
    font-size: 12px;
    line-height: 1.5em;
    color: rgb(50, 50, 50);
}
h1 {
    font-size: 36px;
    line-height: 1.5em;
    font-weight: 700;
    color: rgb(29, 161, 242);
    margin: 0;
    padding: 0;
}
h2 {
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 1.5em;
    font-weight: 600;
    color: rgb(29, 161, 242);
    border: 2px solid rgb(29, 161, 242);
    border-radius: 5px;
    padding-top: 2px;
}
h3 {
    font-size: 16px;
    line-height: 1em;
    font-weight: 600;
    color: rgb(50, 50, 50);
    margin: 5px 0;
    padding: 0;
}
h4 {
    font-size: 12px;
    line-height: 1.5em;
    font-weight: 600;
    color: rgb(50, 50, 50);
    margin: 0;
    padding: 0;
}
a {
    color: inherit;
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
    padding-left: 1em;
}
ul > li:before {
    display: inline-block;
    content: '-';
    width: 1em;
    margin-left: -1em;
}
li {
    margin: 5px 0;
}
.letter-size {
    /* 216mm * 279mm */
    /* 2.54cm = 25.4mm margins */
    background-color: white;
    padding: 25.4mm;
    width: calc(216mm - 25.4mm * 2);
    height: calc(279mm - 25.4mm * 2);
    position: relative;
}
.resume-header {
    position: relative;
    width: 100%;
    height: 8%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.resume-body-wrapper {
    width: 100%;
    height: calc(100% - 8% - 2%);
    margin-top: 2%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.resume-body-left {
    width: calc(60% - 10mm);
    height: 100%;
    margin-right: 10mm;
}
.resume-body-right {
    height: 100%;
    width: 40%;
}
.info {
    font-size: 8px;
    line-height: 8px;
    position: absolute;
    right: 2px;
    bottom: 0;
    color: rgb(230, 230, 230);
}


.name-wrapper {

}
.name-alias {
    color: rgb(180, 180, 180);
}
.contact-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.section-wrapper {
    width: 100%;
}
.item-wrapper {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
}
.item-wrapper:last-child {
    margin-bottom: 0;
}
.items-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 5px 0 10px 0;
}
.item-link {
    color: rgb(120, 120, 120);
}
.item-year {
    position: absolute;
    right: 0;
    top: 3px;
}
.item-wrapper h3 {
    width: 90%;
}

.toolbox-wrapper {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.toolbox-item {
    margin-right: 5px;
}


.latex {
    line-height: 1em;
}
.latex sub, .latex sup {
    text-transform: uppercase;
}

.latex sub {
    vertical-align: -0.5ex;
    margin-left: -0.1667em;
    margin-right: -0.125em;
}

.latex, .latex sub {
    font-size: 1em;
}

.latex sup {
    font-size: 0.85em;
    vertical-align: 0.15em;
    margin-left: -0.36em;
    margin-right: -0.15em;
}
